  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/Raleway-Regular.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: url("./fonts/Raleway-Medium.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  src: url("./fonts/Raleway-SemiBold.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: url("./fonts/Raleway-Bold.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: url("./fonts/Montserrat-ExtraBold.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: url("./fonts/Montserrat-Black.ttf") format("truetype");
  font-display: swap;
}
a {
  text-decoration: none;
}
a:hover {
  color: #ffdd59;
}

body {
  padding-top: 93px;
}

.main-btn {
  color: #337ab7;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  background-color: #ffdd59;
  border: 2px solid #ffdd59;
}

.secondary-btn {
  background-color: transparent;
  border: 2px solid #ffdd59;
  color: #000;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.secondary-c {
  color: #ffdd59;
}

.section-title {
  text-align: center;
  color: #000;
  font-weight: 800;
  font-family: "Montserrat", sans-serif;
  font-size: calc((1vw + 1vh) * 2);
}

.nav-fixed {
  background: #fff;
  position: fixed;
  padding: 15px 0;
  width: 100%;
  z-index: 10;
  top: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}
.nav-fixed__logo img {
  max-width: 100%;
  height: auto;
  display: block;
}
.nav-fixed__logo .mobile-logo {
  display: none;
}
.nav-fixed__container {
  display: flex;
  align-items: center;
}
.nav-fixed__links {
  display: flex;
}
.nav-fixed__burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 30px;
  margin-left: 10px;
}
.nav-fixed__burger span {
  height: 2px;
  width: 100%;
  background-color: #ffdd59;
  transition: all 0.3s;
}
.nav-fixed__burger span:first-child {
  transform-origin: right top;
}
.nav-fixed__burger span:last-child {
  transform-origin: left bottom;
}
.nav-fixed__burger.active span:first-child {
  transform: rotate(-45deg) translate(5px, 10px);
  width: 50%;
}
.nav-fixed__burger.active span:nth-child(2) {
  transform: rotate(45deg);
}
.nav-fixed__burger.active span:last-child {
  width: 50%;
  transform: rotate(-45deg) translate(6px, 50%);
}
.nav-fixed__link {
  font-size: 1.2em;
  padding: 0.5em 1.5em;
}
.nav-fixed__link.active {
  color: #ffdd59;
}
.nav-fixed__phones {
  display: flex;
  flex-direction: column;
}
.nav-fixed__phone {
  color: #000;
  font-size: 1.2em;
  margin-left: 0.5em;
}

#main {
  background-color: #337ab7;
  padding: 30px 0;
  overflow: hidden;
}
#main .section-main-content {
  color: #fff;
  padding: 50px 0;
}
#main .section-main-content__sale {
  font-family: "Raleway", sans-serif;
  font-size: 1.25em;
  font-weight: 400;
  font-feature-settings: "lnum" on;
}
#main .section-main-content__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  letter-spacing: 1px;
  font-size: 3em;
}
#main .section-main-content__features {
  display: flex;
  padding-left: 0;
  margin: gap 0;
}
#main .section-main-content__features li {
  list-style-type: none;
  font-family: "Raleway", sans-serif;
}
#main .section-main-content__features li:not(:first-child) {
  list-style-type: square;
  margin-left: 30px;
}
#main .section-main-content__features li:not(:first-child)::marker {
  color: #ffdd59;
}
#main .section-main-content__buttons button {
  padding: 10px;
  width: 30%;
}
#main .section-main-content__buttons a button {
  width: 60%;
  margin-left: 3%;
}

#catalog {
  padding: 0;
}

#advantages {
  padding: 0;
}

#waranty {
  padding: 0;
}

#delivery {
  padding: 0;
}

#contacts {
  padding: 0;
}

.shiny-btn {
  position: relative;
  overflow: hidden;
}

.shiny-btn:after {
  content: "";
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.4);
  width: 8%;
  height: 100%;
  top: 0;
  left: 0;
  transform: skewX(-50deg) translateX(-110%);
  filter: blur(2px);
  transform-origin: top right;
  animation: shine 5s infinite;
  animation-delay: 2s;
}

.pulse-btn {
  animation: pulse 2s infinite;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes shine {
  0% {
    transform: skewX(-50deg) translateX(-130%);
  }
  50% {
    transform-origin: left bottom;
    transform: skewX(-50deg) translateX(1310%);
  }
  100% {
    transform-origin: left bottom;
    transform: skewX(-50deg) translateX(1310%);
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.95, 0.95);
  }
  70% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.95);
  }
}
@media screen and (max-width: 1400px) {
  .nav-fixed__link {
    padding: 0.5em 0.8em;
  }
}
@media screen and (max-width: 1200px) {
  .nav-fixed__link {
    padding: 0.5em;
    font-size: 1.1em;
  }
  .nav-fixed__phone {
    font-size: 1em;
  }
}
@media screen and (max-width: 992px) {
  .nav-fixed__links {
    display: none;
  }
}
@media screen and (max-width: 480px) {
  .nav-fixed .desktop-logo {
    display: none;
  }
  .nav-fixed .mobile-logo {
    display: block;
  }
  .nav-fixed__burger {
    display: flex;
  }
  .nav-fixed__links {
    transition: max-height 0.4s;
    max-height: 0;
    overflow: hidden;
    height: auto;
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 100%;
    align-items: center;
    width: 100%;
    background-color: #337ab7;
    font-size: 1.3em;
  }
  .nav-fixed__links.active {
    max-height: 600px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  }
}